import React, { useState, useContext, useEffect } from 'react';
import { AuthContext, AlertContext } from '../../App';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useNavigate } from "react-router-dom";
import { api } from '../../api';

function AddPet() {
  const { user, isLoggedIn, loginUser } = useContext(AuthContext);
  const { showAlert } = useContext(AlertContext);
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    //  
    name: '',
    phone: '',
    email: '',
    register: isLoggedIn ? 0 : 0,
    password: '',
    password_confirmation: '',
    
    //  
    kind: '',
    district: '',
    mark: '',
    description: '',
    
    // 
    photo1: null,
    photo2: null,
    photo3: null,
    
    // 
    confirm: false
  });

  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [filePreviews, setFilePreviews] = useState({});

  //     
  useEffect(() => {
    if (isLoggedIn && user) {
      setFormData(prev => ({
        ...prev,
        name: user?.name || '',
        phone: user?.phone || '',
        email: user?.email || '',
        register: 0 //    register=0
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        register: 0, //    0
        password: '',
        password_confirmation: ''
      }));
    }
  }, [isLoggedIn, user]);

  //   
  const handleInputChange = (e) => {
    const { id, value, type, checked } = e.target;
    
    //     (  )
    if (id === 'register' && !isLoggedIn) {
      setFormData(prev => ({
        ...prev,
        [id]: checked ? 1 : 0,
        password: '',
        password_confirmation: ''
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [id]: type === 'checkbox' ? checked : value
      }));
    }
  };

  const handleFileChange = (e) => {
    const { id, files } = e.target;
    const file = files[0];
    
    if (file) {
      //    ( PNG)
      if (!file.type.includes('png')) {
        showAlert(',   PNG ', 'warning');
        e.target.value = '';
        return;
      }
      
      //   
      if (file.size > 5 * 1024 * 1024) {
        showAlert('     5MB', 'warning');
        e.target.value = '';
        return;
      }
      
      //  
      const reader = new FileReader();
      reader.onload = (event) => {
        setFilePreviews(prev => ({
          ...prev,
          [id]: event.target.result
        }));
      };
      reader.readAsDataURL(file);
      
      setFormData(prev => ({
        ...prev,
        [id]: file
      }));
    } else {
      //  
      setFilePreviews(prev => {
        const newPreviews = { ...prev };
        delete newPreviews[id];
        return newPreviews;
      });
      
      setFormData(prev => ({
        ...prev,
        [id]: null
      }));
    }
  };

  //  
  const validatePassword = (password) => {
    if (password.length < 7) return '    7 ';
    if (!/\d/.test(password)) return '      ';
    if (!/[a-z]/.test(password)) return '       ';
    if (!/[A-Z]/.test(password)) return '       ';
    return null;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErrors({});

    //  
    const newErrors = {};
    
    //  
    if (!formData.name.trim()) newErrors.name = ' ';
    else if (!/^[--\s-]+$/.test(formData.name)) {
      newErrors.name = '    ,   ';
    }
    
    if (!formData.phone.trim()) newErrors.phone = ' ';
    else if (!/^[+\d]+$/.test(formData.phone)) {
      newErrors.phone = '       +';
    }
    
    if (!formData.email.trim()) newErrors.email = 'Email ';
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = '  email ';
    }
    
    if (!formData.kind) newErrors.kind = '  ';
    if (!formData.district) newErrors.district = ' ';
    if (!formData.description.trim()) newErrors.description = ' ';
    if (!formData.photo1) newErrors.photo1 = '  ';
    if (!formData.confirm) newErrors.confirm = '     ';
    
    //    :    
    if (isLoggedIn) {
      if (!formData.password) {
        newErrors.password = '     ';
      } else if (formData.password !== formData.password_confirmation) {
        newErrors.password_confirmation = '  ';
      }
    }
    
    //    :    
    if (!isLoggedIn && formData.register === 1) {
      if (!formData.password) {
        newErrors.password = ' ';
      } else {
        const passwordError = validatePassword(formData.password);
        if (passwordError) newErrors.password = passwordError;
      }
      
      if (!formData.password_confirmation) {
        newErrors.password_confirmation = '  ';
      } else if (formData.password !== formData.password_confirmation) {
        newErrors.password_confirmation = '  ';
      }
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      setLoading(false);
      showAlert(',    ', 'warning');
      return;
    }

    try {
      // 1.        (register=1)
      if (!isLoggedIn && formData.register === 1) {
        try {
          //  
          const registrationData = {
            name: formData.name.trim(),
            phone: formData.phone.replace(/\D/g, ''),
            email: formData.email.trim().toLowerCase(),
            password: formData.password,
            password_confirmation: formData.password_confirmation,
            confirm: formData.confirm ? 1 : 0
          };
          
          console.log('    :', registrationData);
          
          const registrationResponse = await api.register(registrationData);
          console.log(' :', registrationResponse);
          
          //   
          try {
            const loginResponse = await api.login(registrationData.email, registrationData.password);
            console.log('   :', loginResponse);
            
            //  
            const userToken = loginResponse.data?.token || loginResponse.token;
            if (userToken) {
              localStorage.setItem('authToken', userToken);
              
              //   
              try {
                const userResponse = await api.getCurrentUser();
                const userData = userResponse.data || userResponse;
                
                //  
                localStorage.setItem('userData', JSON.stringify(userData));
                
                //     
                await loginUser(registrationData.email, registrationData.password);
                
              } catch (userError) {
                console.error('   :', userError);
              }
            }
          } catch (loginError) {
            console.error('   :', loginError);
            showAlert('  ,    . ,  .', 'warning');
          }
        } catch (regError) {
          console.error(' :', regError);
          throw new Error(' : ' + regError.message);
        }
      }

      // 2.  FormData   
      const formDataToSend = new FormData();
      
      //          
      formDataToSend.append('district', formData.district);
      formDataToSend.append('name', formData.name.trim());
      formDataToSend.append('phone', formData.phone.replace(/\D/g, ''));
      formDataToSend.append('email', formData.email.trim().toLowerCase());
      formDataToSend.append('kind', formData.kind);
      
      //  
      if (formData.mark.trim()) {
        formDataToSend.append('mark', formData.mark.trim());
      }
      
      formDataToSend.append('description', formData.description.trim());
      formDataToSend.append('photo1', formData.photo1);
      
      //    
      if (formData.photo2) formDataToSend.append('photo2', formData.photo2);
      if (formData.photo3) formDataToSend.append('photo3', formData.photo3);
      
      // 
      formDataToSend.append('confirm', formData.confirm ? '1' : '0');
      
      // :  register   
      formDataToSend.append('register', formData.register.toString());
      
      //   :
      // 1.   (  )
      // 2.    ()
      // 3.    ( )
      
      if (isLoggedIn || formData.register === 1) {
        //       
        formDataToSend.append('password', formData.password);
        formDataToSend.append('password_confirmation', formData.password_confirmation || formData.password);
      } else {
        //       
        formDataToSend.append('password', '');
        formDataToSend.append('password_confirmation', '');
      }
      
      //  :    FormData
      console.log('  FormData:');
      for (let [key, value] of formDataToSend.entries()) {
        if (key.startsWith('photo')) {
          console.log(`${key}: [] ${value.name} (${value.type}, ${value.size} )`);
        } else {
          console.log(`${key}: ${value}`);
        }
      }
      
      // 3.  
      console.log('   API...');
      const response = await api.addPet(formDataToSend);
      console.log('  API   :', response);
      
      // 4.   
let successMessage = '  !';
let newPetId = null;

//   ID  
if (response.data?.order?.id) {
  newPetId = response.data.order.id;
} else if (response.data?.id) {
  newPetId = response.data.id;
} else if (response.data?.order_id) {
  newPetId = response.data.order_id;
} else if (response.id) {
  newPetId = response.id;
} else if (response.order_id) {
  newPetId = response.order_id;
}

if (!isLoggedIn && formData.register === 1) {
  successMessage += '        .';
} else if (isLoggedIn) {
  successMessage += '     .';
}

showAlert(successMessage, 'success');

// 5.     ( )
setFormData(prev => ({
  ...prev,
  password: '',
  password_confirmation: ''
}));

// 6.       ID,   
setTimeout(() => {
  if (newPetId) {
    navigate(`/pet/${newPetId}`);
  } else {
    navigate('/profile');
    showAlert(' ! ID  ,     .', 'info');
  }
}, 1500);
      
    } catch (error) {
      console.error('  :', error);
      
      let errorMessage = '  : ';
      
      if (error.message.includes('422')) {
        errorMessage = '  .     .';
      } else if (error.message.includes('401')) {
        errorMessage = ' . ,   .';
      } else if (error.message.includes('Failed to fetch')) {
        errorMessage = ' .    .';
      } else {
        errorMessage += error.message;
      }
      
      showAlert(errorMessage, 'danger');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div id="add-pet" className="page fade-in active-page">
      <div className="container my-5">
        <h2 className="section-title">    </h2>
        <div className="row justify-content-center">
          <div className="col-lg-10">
            <div className="card">
              <div className="card-body p-4">
                <form id="add-pet-form" onSubmit={handleSubmit} noValidate>
                  <div className="row g-3">
                    {/*     */}
                    <div className="col-md-6">
                      <label htmlFor="name" className="form-label">
                          *
                      </label>
                      <input
                        type="text"
                        className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                        id="name"
                        value={formData.name}
                        onChange={handleInputChange}
                        required
                        pattern="[--\s-]+"
                        placeholder=" "
                      />
                      {errors.name && <div className="invalid-feedback">{errors.name}</div>}
                      <div className="form-text"> ,   </div>
                    </div>
                    
                    <div className="col-md-6">
                      <label htmlFor="phone" className="form-label">
                         *
                      </label>
                      <input
                        type="tel"
                        className={`form-control ${errors.phone ? 'is-invalid' : ''}`}
                        id="phone"
                        value={formData.phone}
                        onChange={handleInputChange}
                        required
                        pattern="[+\d]+"
                        placeholder="+79111234567"
                      />
                      {errors.phone && <div className="invalid-feedback">{errors.phone}</div>}
                      <div className="form-text">    +</div>
                    </div>
                    
                    <div className="col-12">
                      <label htmlFor="email" className="form-label">
                        Email *
                      </label>
                      <input
                        type="email"
                        className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                        id="email"
                        value={formData.email}
                        onChange={handleInputChange}
                        required
                        placeholder="user@example.ru"
                      />
                      {errors.email && <div className="invalid-feedback">{errors.email}</div>}
                    </div>
                    
                    {/*   :      */}
                    {isLoggedIn && (
                      <div className="col-12">
                        <div className="alert alert-warning mb-3">
                          <i className="bi bi-shield-exclamation me-2"></i>
                          <strong>    </strong>
                          <div className="mt-1">
                                   
                          </div>
                        </div>
                        
                        <div className="row g-3">
                          <div className="col-md-6">
                            <label htmlFor="password" className="form-label">
                                 *
                            </label>
                            <input
                              type="password"
                              className={`form-control ${errors.password ? 'is-invalid' : ''}`}
                              id="password"
                              value={formData.password}
                              onChange={handleInputChange}
                              placeholder="  "
                              required
                            />
                            {errors.password && <div className="invalid-feedback">{errors.password}</div>}
                            <div className="form-text">
                                    
                            </div>
                          </div>
                          
                          <div className="col-md-6">
                            <label htmlFor="password_confirmation" className="form-label">
                                *
                            </label>
                            <input
                              type="password"
                              className={`form-control ${errors.password_confirmation ? 'is-invalid' : ''}`}
                              id="password_confirmation"
                              value={formData.password_confirmation}
                              onChange={handleInputChange}
                              placeholder=" "
                              required
                            />
                            {errors.password_confirmation && (
                              <div className="invalid-feedback">{errors.password_confirmation}</div>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                    
                    {/*   -    */}
                    {!isLoggedIn && (
                      <div className="col-12">
                        <div className="form-check form-switch">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="register"
                            checked={formData.register === 1}
                            onChange={(e) => handleInputChange({
                              target: {
                                id: 'register',
                                type: 'checkbox',
                                checked: e.target.checked
                              }
                            })}
                          />
                          <label className="form-check-label" htmlFor="register">
                            <strong>  </strong> ()
                          </label>
                          <div className="form-text">
                                      
                          </div>
                        </div>
                      </div>
                    )}
                    
                    {/*    -     */}
                    {!isLoggedIn && formData.register === 1 && (
                      <div className="col-12" id="password-fields">
                        <div className="alert alert-info mb-3">
                          <i className="bi bi-info-circle me-2"></i>
                                  
                        </div>
                        <div className="row g-3">
                          <div className="col-md-6">
                            <label htmlFor="password" className="form-label">
                               *
                            </label>
                            <input
                              type="password"
                              className={`form-control ${errors.password ? 'is-invalid' : ''}`}
                              id="password"
                              value={formData.password}
                              onChange={handleInputChange}
                              minLength={7}
                              pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{7,}$"
                              placeholder="123"
                              required
                            />
                            {errors.password ? (
                              <div className="invalid-feedback">{errors.password}</div>
                            ) : (
                              <div className="form-text">
                                 7 : 1 , 1   1  
                              </div>
                            )}
                          </div>
                          
                          <div className="col-md-6">
                            <label htmlFor="password_confirmation" className="form-label">
                                *
                            </label>
                            <input
                              type="password"
                              className={`form-control ${errors.password_confirmation ? 'is-invalid' : ''}`}
                              id="password_confirmation"
                              value={formData.password_confirmation}
                              onChange={handleInputChange}
                              placeholder="123"
                              required
                            />
                            {errors.password_confirmation && (
                              <div className="invalid-feedback">{errors.password_confirmation}</div>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                    
                    {/*    */}
                    <div className="col-12 mt-4">
                      <h5 className="mb-3">  </h5>
                    </div>
                    
                    <div className="col-md-6">
                      <label htmlFor="kind" className="form-label">
                          *
                      </label>
                      <select 
                        className={`form-select ${errors.kind ? 'is-invalid' : ''}`} 
                        id="kind" 
                        value={formData.kind}
                        onChange={handleInputChange}
                        required
                      >
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                      {errors.kind && <div className="invalid-feedback">{errors.kind}</div>}
                    </div>
                    
                    <div className="col-md-6">
                      <label htmlFor="district" className="form-label">
                        ,    *
                      </label>
                      <select 
                        className={`form-select ${errors.district ? 'is-invalid' : ''}`} 
                        id="district" 
                        value={formData.district}
                        onChange={handleInputChange}
                        required
                      >
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                      {errors.district && <div className="invalid-feedback">{errors.district}</div>}
                    </div>
                    
                    <div className="col-12">
                      <label htmlFor="mark" className="form-label">
                        / ( )
                      </label>
                      <input 
                        type="text" 
                        className="form-control" 
                        id="mark" 
                        value={formData.mark}
                        onChange={handleInputChange}
                        placeholder=": VL-0214"
                      />
                      <div className="form-text">
                            ,     
                      </div>
                    </div>
                    
                    <div className="col-12">
                      <label htmlFor="description" className="form-label">
                         *
                      </label>
                      <textarea
                        className={`form-control ${errors.description ? 'is-invalid' : ''}`}
                        id="description"
                        rows={5}
                        value={formData.description}
                        onChange={handleInputChange}
                        required
                        placeholder=" : , ,  , , ,    .."
                      />
                      {errors.description && <div className="invalid-feedback">{errors.description}</div>}
                      <div className="form-text">
                             
                      </div>
                    </div>
                    
                    {/*  */}
                    <div className="col-12">
                      <h5 className="mb-3"> </h5>
                      <div className="row g-3">
                        <div className="col-md-6 col-lg-4">
                          <label htmlFor="photo1" className="form-label">
                              *
                          </label>
                          <input
                            className={`form-control ${errors.photo1 ? 'is-invalid' : ''}`}
                            type="file"
                            id="photo1"
                            accept="image/png"
                            onChange={handleFileChange}
                            required
                          />
                          {errors.photo1 && <div className="invalid-feedback">{errors.photo1}</div>}
                          <div className="form-text">
                              (, PNG,  5MB)
                          </div>
                          {filePreviews.photo1 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo1} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '150px', maxHeight: '150px' }}
                              />
                            </div>
                          )}
                        </div>
                        
                        <div className="col-md-6 col-lg-4">
                          <label htmlFor="photo2" className="form-label">
                             
                          </label>
                          <input
                            className="form-control"
                            type="file"
                            id="photo2"
                            accept="image/png"
                            onChange={handleFileChange}
                          />
                          <div className="form-text">  (PNG,  5MB)</div>
                          {filePreviews.photo2 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo2} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '150px', maxHeight: '150px' }}
                              />
                            </div>
                          )}
                        </div>
                        
                        <div className="col-md-6 col-lg-4">
                          <label htmlFor="photo3" className="form-label">
                             
                          </label>
                          <input
                            className="form-control"
                            type="file"
                            id="photo3"
                            accept="image/png"
                            onChange={handleFileChange}
                          />
                          <div className="form-text">  (PNG,  5MB)</div>
                          {filePreviews.photo3 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo3} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '150px', maxHeight: '150px' }}
                              />
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                    
                    {/*  */}
                    <div className="col-12">
                      <div className="form-check">
                        <input
                          className={`form-check-input ${errors.confirm ? 'is-invalid' : ''}`}
                          type="checkbox"
                          id="confirm"
                          checked={formData.confirm}
                          onChange={handleInputChange}
                          required
                        />
                        <label className="form-check-label" htmlFor="confirm">
                          <strong>      *</strong>
                        </label>
                        {errors.confirm && <div className="invalid-feedback">{errors.confirm}</div>}
                      </div>
                    </div>
                    
                    {/*   */}
                    <div className="col-12 mt-4">
                      <div className="d-grid">
                        <button
                          type="submit"
                          className="btn btn-primary btn-lg btn-animated"
                          disabled={loading}
                        >
                          {loading ? (
                            <>
                              <span className="spinner-border spinner-border-sm me-2" role="status"></span>
                              {!isLoggedIn && formData.register === 1 ? '  ...' : '...'}
                            </>
                          ) : (
                            <>
                              <i className="bi bi-plus-circle me-2"></i>
                              {!isLoggedIn && formData.register === 1 ? '   ' : ' '}
                            </>
                          )}
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddPet;