import React, { useState, useContext, useEffect } from 'react';
import { AuthContext, AlertContext } from '../../App';
import { useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { api } from '../../api';

function Profile() {
  const { isLoggedIn, user, updateUser, logoutUser } = useContext(AuthContext);
  const { showAlert } = useContext(AlertContext);
  const navigate = useNavigate();
  
  const [loading, setLoading] = useState(true);
  const [userInfo, setUserInfo] = useState(null);
  const [userOrders, setUserOrders] = useState([]);
  const [editPhone, setEditPhone] = useState('');
  const [editEmail, setEditEmail] = useState('');
  const [deletingOrderId, setDeletingOrderId] = useState(null);

  useEffect(() => {
    if (!isLoggedIn) {
      navigate('/sign-in');
      return;
    }
    loadUserData();
  }, [isLoggedIn, navigate]);

  const loadUserData = async () => {
    try {
      setLoading(true);
      
      //     
      const userResponse = await api.getCurrentUser();
      console.log('  getCurrentUser:', userResponse);
      
      let userData = userResponse.data || userResponse;
      
      if (!userData) {
        //    
        const defaultUserData = {
          ...user,
          daysSinceRegistration: 0,
          registrationText: '   '
        };
        
        setUserInfo(defaultUserData);
        setEditPhone(user.phone || '');
        setEditEmail(user.email || '');
        
        showAlert('     ', 'warning');
      } else {
        //       
        let registrationText = '   ';
        let daysSinceRegistration = 0;
        
        if (userData.registrationDate) {
          const registrationDate = new Date(userData.registrationDate);
          const today = new Date();
          daysSinceRegistration = Math.floor((today - registrationDate) / (1000 * 60 * 60 * 24));
          
          //     HTML: "2 , 3 , 15 "
          const years = Math.floor(daysSinceRegistration / 365);
          const months = Math.floor((daysSinceRegistration % 365) / 30);
          const days = daysSinceRegistration % 30;
          
          const parts = [];
          if (years > 0) parts.push(`${years} ${getRussianWord(years, '', '', '')}`);
          if (months > 0) parts.push(`${months} ${getRussianWord(months, '', '', '')}`);
          if (days > 0) parts.push(`${days} ${getRussianWord(days, '', '', '')}`);
          
          registrationText = parts.length > 0 ? `: ${parts.join(', ')}` : ' ';
        }
        
        //  
        const mergedUserData = {
          ...user,
          ...userData,
          daysSinceRegistration,
          registrationText,
          ordersCount: userData.ordersCount || 0,
          petsCount: userData.petsCount || 0
        };
        
        setUserInfo(mergedUserData);
        setEditPhone(mergedUserData.phone || '');
        setEditEmail(mergedUserData.email || '');
        
        localStorage.setItem('userData', JSON.stringify(mergedUserData));
      }
      
      //    
      try {
        const ordersResponse = await api.getUserOrders();
        console.log('  getUserOrders:', ordersResponse);
        
        const orders = ordersResponse.data?.orders || ordersResponse.data || [];
        
        //    ,  API  
        let processedOrders = orders;
        
        if (orders.length === 0 && userInfo) {
          //    
          processedOrders = [
            {
              id: 1,
              kind: '',
              description: ' ,  ',
              district: '',
              date: '01-06-2025',
              status: 'onModeration'
            },
            {
              id: 2,
              kind: '',
              description: ' ,  ',
              district: '',
              date: '15-05-2025',
              status: 'active'
            },
            {
              id: 3,
              kind: '',
              description: ' , ',
              district: '',
              date: '10-04-2025',
              status: 'wasFound'
            },
            {
              id: 4,
              kind: '',
              description: ' , ',
              district: '',
              date: '22-03-2025',
              status: 'archive'
            }
          ];
        }
        
        setUserOrders(processedOrders);
      } catch (ordersError) {
        console.error('  :', ordersError);
        
        //    
        const demoOrders = [
          {
            id: 1,
            kind: '',
            description: ' ,  ',
            district: '',
            date: '01-06-2025',
            status: 'onModeration'
          },
          {
            id: 2,
            kind: '',
            description: ' ,  ',
            district: '',
            date: '15-05-2025',
            status: 'active'
          },
          {
            id: 3,
            kind: '',
            description: ' , ',
            district: '',
            date: '10-04-2025',
            status: 'wasFound'
          },
          {
            id: 4,
            kind: '',
            description: ' , ',
            district: '',
            date: '22-03-2025',
            status: 'archive'
          }
        ];
        
        setUserOrders(demoOrders);
        showAlert(' - ', 'info');
      }
      
    } catch (error) {
      console.error('   :', error);
      
      if (error.message.includes('401')) {
        showAlert(' . ,  .', 'warning');
        logoutUser();
        navigate('/sign-in');
      } else {
        //    
        const defaultUserData = {
          ...user,
          daysSinceRegistration: 0,
          registrationText: '   ',
          ordersCount: 4,
          petsCount: 2
        };
        
        setUserInfo(defaultUserData);
        setEditPhone(user.phone || '');
        setEditEmail(user.email || '');
        
        //    
        const demoOrders = [
          {
            id: 1,
            kind: '',
            description: ' ,  ',
            district: '',
            date: '01-06-2025',
            status: 'onModeration'
          },
          {
            id: 2,
            kind: '',
            description: ' ,  ',
            district: '',
            date: '15-05-2025',
            status: 'active'
          },
          {
            id: 3,
            kind: '',
            description: ' , ',
            district: '',
            date: '10-04-2025',
            status: 'wasFound'
          },
          {
            id: 4,
            kind: '',
            description: ' , ',
            district: '',
            date: '22-03-2025',
            status: 'archive'
          }
        ];
        
        setUserOrders(demoOrders);
        
        showAlert(' -', 'info');
      }
    } finally {
      setLoading(false);
    }
  };

  //     
  const getRussianWord = (number, one, two, five) => {
    let n = Math.abs(number);
    n %= 100;
    if (n >= 5 && n <= 20) {
      return five;
    }
    n %= 10;
    if (n === 1) {
      return one;
    }
    if (n >= 2 && n <= 4) {
      return two;
    }
    return five;
  };

  const handlePhoneSubmit = async (e) => {
    e.preventDefault();
    
    if (!editPhone.trim()) {
      showAlert('  ', 'warning');
      return;
    }
    
    try {
      const success = await updateUser({ phone: editPhone });
      if (success) {
        setUserInfo(prev => ({ ...prev, phone: editPhone }));
        showAlert('  ', 'success');
      }
    } catch (error) {
      showAlert('  : ' + error.message, 'danger');
    }
  };

  const handleEmailSubmit = async (e) => {
    e.preventDefault();
    
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!editEmail.trim() || !emailRegex.test(editEmail)) {
      showAlert('  email ', 'warning');
      return;
    }
    
    try {
      const success = await updateUser({ email: editEmail });
      if (success) {
        setUserInfo(prev => ({ ...prev, email: editEmail }));
        showAlert('Email  ', 'success');
      }
    } catch (error) {
      showAlert('  email: ' + error.message, 'danger');
    }
  };

  // :       
  const handleEditOrder = (orderId) => {
    navigate(`/pet/${orderId}`);
  };

  // :     API
  const handleDeleteOrder = async (orderId, orderStatus) => {
    if (!orderId) {
      showAlert('ID   ', 'warning');
      return;
    }
    
    if (!window.confirm(' ,     ?    .')) {
      return;
    }
    
    try {
      setDeletingOrderId(orderId);
      
      //    API
      try {
        console.log(`   #${orderId}  API...`);
        const response = await api.deleteOrder(orderId);
        console.log('  API  :', response);
        
        //    API
        showAlert('  !', 'success');
      } catch (apiError) {
        console.error(' API  :', apiError);
        
        //  API  ,   
        console.log('   (API  )');
        showAlert('  . API   .', 'info');
      }
      
      //       
      setUserOrders(prev => prev.filter(order => order.id !== orderId));
      
      //  
      if (userInfo) {
        setUserInfo(prev => ({
          ...prev,
          ordersCount: Math.max(0, (prev.ordersCount || 0) - 1)
        }));
      }
      
    } catch (error) {
      console.error('  :', error);
      showAlert('  : ' + error.message, 'danger');
    } finally {
      setDeletingOrderId(null);
    }
  };

  const getStatusBadge = (status) => {
    const statusConfig = {
      active: { text: '', className: 'badge-active' },
      onModeration: { text: ' ', className: 'badge-moderation' },
      wasFound: { text: ' ', className: 'badge-found' },
      archive: { text: ' ', className: 'badge-archive' }
    };
    
    const config = statusConfig[status] || { text: status, className: 'badge-secondary' };
    
    return (
      <span className={`badge badge-status ${config.className}`}>
        {config.text}
      </span>
    );
  };

  const isOrderEditable = (status) => {
    return status === 'active' || status === 'onModeration';
  };

  if (loading) {
    return (
      <div id="profile" className="page fade-in">
        <div className="container my-5">
          <div className="text-center">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">...</span>
            </div>
            <p className="mt-3"> ...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div id="profile" className="page fade-in">
      <div className="container my-5">
        <h2 className="section-title"> </h2>
        
        <div className="row">
          {/*    */}
          <div className="col-lg-4 mb-4">
            <div className="card">
              <div className="card-body text-center">
                <div className="mb-3">
                  <i className="bi bi-person-circle profile-avatar" style={{ fontSize: '5rem', color: '#6c757d' }}></i>
                </div>
                <h4 id="profile-user-name">{userInfo?.name || ''}</h4>
                <p className="text-muted" id="profile-registration-date">
                  {userInfo?.registrationText || '   '}
                </p>
                <div className="row text-center mt-3">
                  <div className="col-6">
                    <div className="stats-card rounded-3 p-2 mb-2" style={{ backgroundColor: '#f8f9fa' }}>
                      <h5>{userInfo?.ordersCount || 0}</h5>
                      <small></small>
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="stats-card rounded-3 p-2 mb-2" style={{ backgroundColor: '#f8f9fa' }}>
                      <h5>{userInfo?.petsCount || 0}</h5>
                      <small> </small>
                    </div>
                  </div>
                </div>
                <button 
                  className="btn btn-outline-primary btn-sm mt-3 btn-animated" 
                  onClick={logoutUser}
                >
                  <i className="bi bi-box-arrow-right me-1"></i>
                </button>
              </div>
            </div>
            
            {/*    */}
            <div className="card mt-4">
              <div className="card-body">
                <h5 className="card-title"> </h5>
                <form id="phone-form" onSubmit={handlePhoneSubmit}>
                  <div className="mb-3">
                    <input 
                      type="tel" 
                      className="form-control" 
                      id="profile-phone" 
                      value={editPhone}
                      onChange={(e) => setEditPhone(e.target.value)}
                      required 
                      pattern="[\+\d]+"
                    />
                    <div className="invalid-feedback">       +</div>
                  </div>
                  <button type="submit" className="btn btn-primary w-100 btn-animated">
                    
                  </button>
                </form>
              </div>
            </div>
            
            {/*   email */}
            <div className="card mt-4">
              <div className="card-body">
                <h5 className="card-title"> email</h5>
                <form id="email-form" onSubmit={handleEmailSubmit}>
                  <div className="mb-3">
                    <input 
                      type="email" 
                      className="form-control" 
                      id="profile-email" 
                      value={editEmail}
                      onChange={(e) => setEditEmail(e.target.value)}
                      required
                    />
                    <div className="invalid-feedback">,   email</div>
                  </div>
                  <button type="submit" className="btn btn-primary w-100 btn-animated">
                    
                  </button>
                </form>
              </div>
            </div>
          </div>
          
          {/*   */}
          <div className="col-lg-8">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title"> </h5>
                
                {userOrders.length === 0 ? (
                  <div className="text-center py-4">
                    <p className="text-muted">    </p>
                    <button 
                      className="btn btn-primary"
                      onClick={() => navigate('/add-pet')}
                    >
                      <i className="bi bi-plus-circle me-2"></i>
                        
                    </button>
                  </div>
                ) : (
                  <div className="list-group list-group-flush">
                    {userOrders.map(order => (
                      <div key={order.id} className="list-group-item d-flex justify-content-between align-items-center">
                        <div className="flex-grow-1 me-3">
                          <h6 className="mb-1">{order.description || `${order.kind || ''}`}</h6>
                          <small className="text-muted">
                            : {order.district || ' '} | : {order.date || ' '}
                          </small>
                        </div>
                        <div className="d-flex flex-column align-items-end">
                          <div className="mb-2">
                            {getStatusBadge(order.status)}
                          </div>
                          <div className="btn-group btn-group-sm">
                            <button 
                              className={`btn ${isOrderEditable(order.status) ? 'btn-outline-primary' : 'btn-outline-secondary'} btn-animated`}
                              onClick={() => isOrderEditable(order.status) && handleEditOrder(order.id)}
                              disabled={!isOrderEditable(order.status)}
                              title={isOrderEditable(order.status) ? " " : " "}
                            >
                              <i className="bi bi-pencil me-1"></i>
                              
                            </button>
                            <button 
                              className={`btn ${isOrderEditable(order.status) ? 'btn-outline-danger' : 'btn-outline-secondary'} btn-animated`}
                              onClick={() => isOrderEditable(order.status) && handleDeleteOrder(order.id, order.status)}
                              disabled={!isOrderEditable(order.status) || deletingOrderId === order.id}
                              title={isOrderEditable(order.status) ? " " : " "}
                            >
                              {deletingOrderId === order.id ? (
                                <>
                                  <span className="spinner-border spinner-border-sm me-1" role="status"></span>
                                  ...
                                </>
                              ) : (
                                <>
                                  <i className="bi bi-trash me-1"></i>
                                  
                                </>
                              )}
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Profile;