import React, { useState, useContext, useEffect, useCallback } from 'react';
import { AuthContext, AlertContext } from '../../App';
import { useParams, useNavigate, Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { api, getImageUrl } from '../../api';

function Card() {
  const { id } = useParams();
  const { isLoggedIn, user } = useContext(AuthContext);
  const { showAlert } = useContext(AlertContext);
  const navigate = useNavigate();
  
  const [pet, setPet] = useState(null);
  const [isOwner, setIsOwner] = useState(false);
  const [loading, setLoading] = useState(true);
  const [editing, setEditing] = useState(false);
  const [editForm, setEditForm] = useState({
    description: '',
    district: '',
    kind: '',
    mark: '',
  });
  const [editPhotos, setEditPhotos] = useState({
    photo1: null,
    photo2: null,
    photo3: null,
  });
  const [filePreviews, setFilePreviews] = useState({});
  const [editLoading, setEditLoading] = useState(false);

  //   
  const loadPet = useCallback(async () => {
    try {
      setLoading(true);
      const response = await api.getPet(id);
      console.log('Pet data:', response);
      
      //    
      let petData = null;
      
      if (response.data?.pet?.[0]) {
        petData = response.data.pet[0];
      } else if (response.data?.pet) {
        petData = response.data.pet;
      } else if (response.data?.order) {
        petData = response.data.order;
      } else if (response.data) {
        petData = response.data;
      } else {
        petData = response;
      }
      
      //  
      const processedPet = {
        ...petData,
        image: getImageUrl(petData.image),
        photos: petData.photos?.map(photo => getImageUrl(photo)) || []
      };
      
      setPet(processedPet);
      
      //    ( )
      setEditForm({
        description: processedPet.description || '',
        district: processedPet.district || '',
        kind: processedPet.kind || '',
        mark: processedPet.mark || '',
      });
      
      // ,     
      if (isLoggedIn && user && petData) {
        //   user_id   email/phone
        const isPetOwner = (
          (petData.user_id && petData.user_id === user.id) ||
          (petData.email && petData.email === user.email) ||
          (petData.phone && petData.phone === user.phone)
        );
        setIsOwner(isPetOwner);
      }
      
    } catch (error) {
      console.error('  :', error);
      showAlert('     ', 'danger');
      setTimeout(() => navigate('/'), 2000);
    } finally {
      setLoading(false);
    }
  }, [id, isLoggedIn, user, navigate, showAlert]);

  useEffect(() => {
    loadPet();
  }, [loadPet]);

  //  
  const handleContact = (method) => {
    if (!pet) return;
    
    if (method === 'call') {
      window.location.href = `tel:${pet.phone || '+79112345678'}`;
    } else if (method === 'email') {
      window.location.href = `mailto:${pet.email || 'user@user.ru'}`;
    }
  };

  //  
  const handleEdit = () => {
    setEditing(true);
  };

  const handleEditChange = (e) => {
    const { name, value } = e.target;
    setEditForm(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //     
  const handlePhotoChange = (e) => {
    const { id, files } = e.target;
    const file = files[0];
    
    if (file) {
      //    ( PNG)
      if (!file.type.includes('png')) {
        showAlert(',   PNG ', 'warning');
        e.target.value = '';
        return;
      }
      
      //   
      if (file.size > 5 * 1024 * 1024) {
        showAlert('     5MB', 'warning');
        e.target.value = '';
        return;
      }
      
      //  
      const reader = new FileReader();
      reader.onload = (event) => {
        setFilePreviews(prev => ({
          ...prev,
          [id]: event.target.result
        }));
      };
      reader.readAsDataURL(file);
      
      setEditPhotos(prev => ({
        ...prev,
        [id]: file
      }));
    } else {
      //  
      setFilePreviews(prev => {
        const newPreviews = { ...prev };
        delete newPreviews[id];
        return newPreviews;
      });
      
      setEditPhotos(prev => ({
        ...prev,
        [id]: null
      }));
    }
  };

  const handleSaveEdit = async () => {
    if (!pet || !isOwner) return;
    
    try {
      setEditLoading(true);
      
      const formData = new FormData();
      formData.append('description', editForm.description);
      formData.append('district', editForm.district);
      formData.append('kind', editForm.kind);
      formData.append('mark', editForm.mark);
      
      //      
      if (editPhotos.photo1) {
        formData.append('photo1', editPhotos.photo1);
      }
      if (editPhotos.photo2) {
        formData.append('photo2', editPhotos.photo2);
      }
      if (editPhotos.photo3) {
        formData.append('photo3', editPhotos.photo3);
      }
      
      console.log(' :', {
        description: editForm.description,
        district: editForm.district,
        kind: editForm.kind,
        mark: editForm.mark,
        hasPhoto1: !!editPhotos.photo1,
        hasPhoto2: !!editPhotos.photo2,
        hasPhoto3: !!editPhotos.photo3
      });
      
      const response = await api.updatePet(pet.id, formData);
      console.log(' :', response);
      
      //     
      setFilePreviews({});
      setEditPhotos({
        photo1: null,
        photo2: null,
        photo3: null,
      });
      
      //  
      await loadPet();
      setEditing(false);
      showAlert('  !', 'success');
      
    } catch (error) {
      console.error(' :', error);
      showAlert('  : ' + error.message, 'danger');
    } finally {
      setEditLoading(false);
    }
  };

  const handleCancelEdit = () => {
    setEditing(false);
    //   
    setFilePreviews({});
    setEditPhotos({
      photo1: null,
      photo2: null,
      photo3: null,
    });
    
    //    ( )
    if (pet) {
      setEditForm({
        description: pet.description || '',
        district: pet.district || '',
        kind: pet.kind || '',
        mark: pet.mark || ''
      });
    }
  };

  //  
  const handleDelete = async () => {
    if (!pet || !isOwner) return;
    
    if (!window.confirm(' ,     ?    .')) {
      return;
    }
    
    try {
      setLoading(true);
      await api.deleteOrder(pet.id);
      showAlert('  !', 'success');
      setTimeout(() => navigate('/profile'), 1000);
    } catch (error) {
      console.error(' :', error);
      showAlert('  : ' + error.message, 'danger');
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="page fade-in">
        <div className="container my-5">
          <div className="text-center">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">...</span>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!pet) {
    return (
      <div className="page fade-in">
        <div className="container my-5">
          <div className="text-center">
            <h3>  </h3>
            <button className="btn btn-primary mt-3" onClick={() => navigate('/')}>
                
            </button>
          </div>
        </div>
      </div>
    );
  }

  const photos = pet.photos && pet.photos.length > 0 
    ? pet.photos 
    : pet.image 
      ? [pet.image] 
      : ['https://via.placeholder.com/600x400?text=+'];

  const hasMultiplePhotos = photos.length > 1;

  //     (  )
  const getStatusBadge = (status) => {
    switch(status) {
      case 'onModeration':
        return <span className="badge bg-warning"> </span>;
      case 'active':
        return <span className="badge bg-success"></span>;
      case 'wasFound':
        return <span className="badge bg-info"> </span>;
      case 'archive':
        return <span className="badge bg-secondary"> </span>;
      default:
        return null;
    }
  };

  return (
    <div id="pet-card" className="page fade-in">
      <div className="container my-5">
        {/*     */}
        {isOwner && (
          <div className="row mb-4">
            <div className="col-12">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title"> </h5>
                  <div className="d-flex gap-2 flex-wrap">
                    {!editing ? (
                      <>
                        <button 
                          className="btn btn-primary"
                          onClick={handleEdit}
                        >
                          <i className="bi bi-pencil me-2"></i>
                          
                        </button>
                        <button 
                          className="btn btn-danger"
                          onClick={handleDelete}
                          disabled={loading}
                        >
                          <i className="bi bi-trash me-2"></i>
                          
                        </button>
                        <Link to="/profile" className="btn btn-outline-secondary">
                          <i className="bi bi-arrow-left me-2"></i>
                            
                        </Link>
                      </>
                    ) : (
                      <>
                        <button 
                          className="btn btn-success"
                          onClick={handleSaveEdit}
                          disabled={editLoading}
                        >
                          {editLoading ? (
                            <>
                              <span className="spinner-border spinner-border-sm me-2" role="status"></span>
                              ...
                            </>
                          ) : (
                            <>
                              <i className="bi bi-check me-2"></i>
                              
                            </>
                          )}
                        </button>
                        <button 
                          className="btn btn-secondary"
                          onClick={handleCancelEdit}
                          disabled={editLoading}
                        >
                          <i className="bi bi-x me-2"></i>
                          
                        </button>
                      </>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
        
        <div className="row">
          <div className="col-lg-8">
            {/*   */}
            {photos.length > 0 && (
              <div
                id="petCarousel"
                className={`carousel slide mb-4 ${hasMultiplePhotos ? '' : 'carousel-fixed'}`}
                data-bs-ride="carousel"
              >
                {hasMultiplePhotos && (
                  <div className="carousel-indicators">
                    {photos.map((_, index) => (
                      <button
                        key={index}
                        type="button"
                        data-bs-target="#petCarousel"
                        data-bs-slide-to={index}
                        className={index === 0 ? "active" : ""}
                        aria-label={` ${index + 1}`}
                      />
                    ))}
                  </div>
                )}
                
                <div className="carousel-inner rounded-3">
                  {photos.map((photo, index) => (
                    <div key={index} className={`carousel-item ${index === 0 ? "active" : ""}`}>
                      <img
                        src={photo}
                        className="d-block w-100"
                        alt={`${pet.kind}  ${index + 1}`}
                        style={{ height: 400, objectFit: "cover" }}
                        onError={(e) => {
                          e.target.src = 'https://via.placeholder.com/600x400?text=+';
                        }}
                      />
                    </div>
                  ))}
                </div>
                
                {hasMultiplePhotos && (
                  <>
                    <button
                      className="carousel-control-prev"
                      type="button"
                      data-bs-target="#petCarousel"
                      data-bs-slide="prev"
                    >
                      <span className="carousel-control-prev-icon" />
                      <span className="visually-hidden"></span>
                    </button>
                    <button
                      className="carousel-control-next"
                      type="button"
                      data-bs-target="#petCarousel"
                      data-bs-slide="next"
                    >
                      <span className="carousel-control-next-icon" />
                      <span className="visually-hidden"></span>
                    </button>
                  </>
                )}
              </div>
            )}
            
            {/*   -      */}
            <div className="card">
              <div className="card-body">
                {editing ? (
                  //   ( )
                  <>
                    <div className="mb-3">
                      <label htmlFor="kind" className="form-label">
                        <strong>  *</strong>
                      </label>
                      <select 
                        className="form-select" 
                        id="kind" 
                        name="kind"
                        value={editForm.kind}
                        onChange={handleEditChange}
                        required
                      >
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                    
                    <div className="mb-3">
                      <label htmlFor="district" className="form-label">
                        <strong> *</strong>
                      </label>
                      <select 
                        className="form-select" 
                        id="district" 
                        name="district"
                        value={editForm.district}
                        onChange={handleEditChange}
                        required
                      >
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                    
                    <div className="mb-3">
                      <label htmlFor="mark" className="form-label">
                        <strong>/</strong>
                      </label>
                      <input 
                        type="text" 
                        className="form-control" 
                        id="mark" 
                        name="mark"
                        value={editForm.mark}
                        onChange={handleEditChange}
                        placeholder=": VL-0214"
                      />
                    </div>
                    
                    <div className="mb-3">
                      <label htmlFor="description" className="form-label">
                        <strong> *</strong>
                      </label>
                      <textarea
                        className="form-control"
                        id="description"
                        name="description"
                        rows={5}
                        value={editForm.description}
                        onChange={handleEditChange}
                        required
                        placeholder=" ..."
                      />
                    </div>
                    
                    {/*      */}
                    <div className="mb-3">
                      <label className="form-label">
                        <strong>/ </strong>
                      </label>
                      <div className="alert alert-info">
                        <i className="bi bi-info-circle me-2"></i>
                            .   .  5MB,  PNG.
                      </div>
                      <div className="row g-3">
                        <div className="col-md-4">
                          <label htmlFor="photo1" className="form-label">
                             1
                          </label>
                          <input
                            className="form-control"
                            type="file"
                            id="photo1"
                            accept="image/png"
                            onChange={handlePhotoChange}
                          />
                          {filePreviews.photo1 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo1} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '100px', maxHeight: '100px' }}
                              />
                            </div>
                          )}
                        </div>
                        
                        <div className="col-md-4">
                          <label htmlFor="photo2" className="form-label">
                             2
                          </label>
                          <input
                            className="form-control"
                            type="file"
                            id="photo2"
                            accept="image/png"
                            onChange={handlePhotoChange}
                          />
                          {filePreviews.photo2 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo2} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '100px', maxHeight: '100px' }}
                              />
                            </div>
                          )}
                        </div>
                        
                        <div className="col-md-4">
                          <label htmlFor="photo3" className="form-label">
                             3
                          </label>
                          <input
                            className="form-control"
                            type="file"
                            id="photo3"
                            accept="image/png"
                            onChange={handlePhotoChange}
                          />
                          {filePreviews.photo3 && (
                            <div className="mt-2">
                              <img 
                                src={filePreviews.photo3} 
                                alt="" 
                                className="img-thumbnail" 
                                style={{ maxWidth: '100px', maxHeight: '100px' }}
                              />
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  </>
                ) : (
                  //  
                  <>
                    <div className="d-flex justify-content-between align-items-start mb-3">
                      <h3 className="card-title mb-0">{pet.kind}</h3>
                      {/*       */}
                      {pet.status && (
                        <div>
                          {getStatusBadge(pet.status)}
                        </div>
                      )}
                    </div>
                    
                    <div className="mt-3">
                      <p className="card-text">{pet.description || ' '}</p>
                    </div>
                    
                    <div className="row mt-4">
                      <div className="col-md-6">
                        <h5>  </h5>
                        <ul className="list-group list-group-flush">
                          <li className="list-group-item d-flex justify-content-between">
                            <span>:</span>
                            <span>{pet.kind}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span>:</span>
                            <span>{pet.breed || ' '}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span>/:</span>
                            <span>{pet.mark || ' '}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span>:</span>
                            <span>{pet.district || ' '}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span> :</span>
                            <span>{pet.date || ' '}</span>
                          </li>
                        </ul>
                      </div>
                      <div className="col-md-6">
                        <h5> </h5>
                        <ul className="list-group list-group-flush">
                          <li className="list-group-item d-flex justify-content-between">
                            <span>:</span>
                            <span>{pet.name || ' '}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span>:</span>
                            <span>{pet.phone || ' '}</span>
                          </li>
                          <li className="list-group-item d-flex justify-content-between">
                            <span>Email:</span>
                            <span>{pet.email || ' '}</span>
                          </li>
                          {/*       */}
                        </ul>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
          
          <div className="col-lg-4">
            {/*   */}
            <div className="card mb-4">
              <div className="card-body text-center">
                <h5 className="card-title">  ?</h5>
                <p className="card-text">
                          
                </p>
                
                {/*      */}
                {pet.status === 'onModeration' && (
                  <div className="alert alert-warning mb-3">
                    <i className="bi bi-exclamation-triangle me-2"></i>
                      .       .
                  </div>
                )}
                
                {/*      */}
                {isOwner && pet.status === 'onModeration' && (
                  <div className="alert alert-info mb-3">
                    <i className="bi bi-info-circle me-2"></i>
                       .     .
                  </div>
                )}
                
                <button 
                  className="btn btn-primary w-100 mb-2 btn-animated"
                  onClick={() => handleContact('call')}
                >
                  <i className="bi bi-telephone me-2" />
                  
                </button>
                <button 
                  className="btn btn-outline-primary w-100 btn-animated"
                  onClick={() => handleContact('email')}
                >
                  <i className="bi bi-envelope me-2" />
                   email
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Card;