import React, { useState, useContext } from 'react';
import { AuthContext, AlertContext } from '../../App';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { Link, useNavigate } from "react-router-dom";

function Registration() {
  const { registerUser } = useContext(AuthContext);
  const { showAlert } = useContext(AlertContext);
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    email: '',
    password: '',
    confirmPassword: '',
    agree: false
  });

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e) => {
    const { id, value, type, checked } = e.target;
    
    //     
    let processedValue = value;
    if (id === 'phone') {
      processedValue = value.replace(/\D/g, '');
    }
    
    setFormData(prev => ({
      ...prev,
      [id]: type === 'checkbox' ? checked : processedValue
    }));
    
    //    
    if (errors[id]) {
      setErrors(prev => ({ ...prev, [id]: '' }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    //  
    if (!formData.name.trim()) {
      newErrors.name = ' ';
    } else if (!/^[--\s-]+$/.test(formData.name)) {
      newErrors.name = '    ,   ';
    }
    
    //  
    if (!formData.phone.trim()) {
      newErrors.phone = ' ';
    } else if (formData.phone.replace(/\D/g, '').length < 11) {
      newErrors.phone = '    11 ';
    }
    
    //  email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!formData.email.trim()) {
      newErrors.email = 'Email ';
    } else if (!emailRegex.test(formData.email)) {
      newErrors.email = '  email ';
    }
    
    //  
    if (!formData.password) {
      newErrors.password = ' ';
    } else if (formData.password.length < 7) {
      newErrors.password = '    7 ';
    } else if (!/\d/.test(formData.password)) {
      newErrors.password = '      ';
    } else if (!/[a-z]/.test(formData.password)) {
      newErrors.password = '       ';
    } else if (!/[A-Z]/.test(formData.password)) {
      newErrors.password = '       ';
    }
    
    //   
    if (!formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
    } else if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = '  ';
    }
    
    // 
    if (!formData.agree) {
      newErrors.agree = '     ';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);
    
    //      API
    const userData = {
      name: formData.name.trim(),
      phone: formData.phone.replace(/\D/g, ''), //  
      email: formData.email.trim().toLowerCase(),
      password: formData.password,
      password_confirmation: formData.confirmPassword,
      confirm: formData.agree ? 1 : 0
    };
    
    console.log('  :', userData);

    try {
      //  registerUser  AuthContext
      const result = await registerUser(userData);
      
      if (result.success) {
        if (result.autoLogin) {
          //   
          navigate('/profile');
        } else {
          //   
          showAlert(' !    .', 'success');
          navigate('/sign-in');
        }
      }
    } catch (error) {
      console.error(' :', error);
      //     registerUser
    } finally {
      setLoading(false);
    }
  };

  return (
    <div id="registration" className="page fade-in active-page">
      <div className="container my-5">
        <div className="row justify-content-center">
          <div className="col-md-8 col-lg-6">
            <div className="card">
              <div className="card-body p-4">
                <h2 className="text-center mb-4"></h2>
                
                <form id="registration-form" onSubmit={handleSubmit} noValidate>
                  <div className="mb-3">
                    <label htmlFor="name" className="form-label">
                       *
                    </label>
                    <input
                      type="text"
                      className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                      id="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      required
                    />
                    {errors.name && (
                      <div className="invalid-feedback">{errors.name}</div>
                    )}
                    <small className="form-text text-muted">
                       ,   
                    </small>
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="phone" className="form-label">
                       *
                    </label>
                    <input
                      type="tel"
                      className={`form-control ${errors.phone ? 'is-invalid' : ''}`}
                      id="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      required
                    />
                    {errors.phone && (
                      <div className="invalid-feedback">{errors.phone}</div>
                    )}
                    <small className="form-text text-muted">
                       ,   7  8,  11 
                    </small>
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">
                      Email *
                    </label>
                    <input
                      type="email"
                      className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                      id="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      required
                    />
                    {errors.email && (
                      <div className="invalid-feedback">{errors.email}</div>
                    )}
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="password" className="form-label">
                       *
                    </label>
                    <input
                      type="password"
                      className={`form-control ${errors.password ? 'is-invalid' : ''}`}
                      id="password"
                      value={formData.password}
                      onChange={handleInputChange}
                      required
                      minLength="7"
                    />
                    {errors.password && (
                      <div className="invalid-feedback">{errors.password}</div>
                    )}
                    <small className="form-text text-muted">
                       7 ,     1 , 1   1  
                    </small>
                  </div>
                  
                  <div className="mb-3">
                    <label htmlFor="confirmPassword" className="form-label">
                        *
                    </label>
                    <input
                      type="password"
                      className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`}
                      id="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleInputChange}
                      required
                    />
                    {errors.confirmPassword && (
                      <div className="invalid-feedback">{errors.confirmPassword}</div>
                    )}
                  </div>
                  
                  <div className="mb-3 form-check">
                    <input
                      className={`form-check-input ${errors.agree ? 'is-invalid' : ''}`}
                      type="checkbox"
                      id="agree"
                      checked={formData.agree}
                      onChange={handleInputChange}
                      required
                    />
                    <label className="form-check-label" htmlFor="agree">
                            *
                    </label>
                    {errors.agree && (
                      <div className="invalid-feedback d-block">{errors.agree}</div>
                    )}
                  </div>
                  
                  <button
                    type="submit"
                    className="btn btn-primary w-100 btn-animated py-2"
                    disabled={loading}
                  >
                    {loading ? (
                      <>
                        <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                        ...
                      </>
                    ) : (
                      ''
                    )}
                  </button>
                  
                  <div className="text-center mt-3">
                    <p>
                        ?{' '}
                      <Link to="/sign-in" className="text-primary">
                        
                      </Link>
                    </p>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Registration;